html,
body,
#root {
  margin: 0;
  padding: 0;
  height: 100%;
  background: #1b2838;
}

.App {
  display: flex;
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  color: #fefefe;
  animation: infinite in 4s linear;
}

@keyframes in {
  0% {
    opacity: 0;
  }
}
